.card {
    width: 150px;
    height: 200px;
    display: inline-block;
    background-size: 100% 100%;
    margin: 8px;
    border: 1px solid black;
    border-radius: 10px;
}

.card-empty {
    border-radius: 10px;
    border: 1px solid black;
    background: lightblue;
}


